.footer {
    height: 300px;
    background-color: #ff7f0d;
    display: flex;
    justify-content: center;
}
.container {
    width: 80%;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 100px 0;
}
.socialBlock{
    display: flex;
    justify-content: space-between;
    width: 500px;
    border: 1px solid ;
}
.socialIcon {
    width: 60px;
    height: 60px;
    background-color: #757575;
}

@media screen and (max-width: 626px ) {
    .socialBlock {
        width: 100%;
    }
}

@media screen and (max-width: 340px ) {
    .socialIcon {
        width: 50px;
        height: 50px;
    }
}