.main {
    min-height: 100vh;
    background-color: #1f1f20;
    display: flex;
    justify-content: center;
    padding: 100px 0;
}

.container{
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.greeting {
    display: flex;
    flex-direction: column;
}

.greeting span:nth-child(1) {
    font-size: 16px;
}

.greeting span:nth-child(2),
.greeting span:nth-child(2) span{
    font-weight: 700;
    font-stretch: normal;
    font-size: 50px;
    line-height: 1.2;
    letter-spacing: 5px;
    font-family: 'Montserrat', sans-serif;
    position: relative;
    z-index: 0;
}
.greeting span:nth-child(2) span:before {
   content: "";
    position: absolute;
    width: 100%;
    height: 20px;
    background-color: blueviolet;
    bottom: 0px;
    z-index: -1;
}

 h1 {
     font-size: 27px ;
 }

.photo {
    margin-top: 50px;
    position: relative;
    z-index: 0;
}

.image {
    width: 300px;
    height: 400px;
    background-color: #bcbcbc;
}

.photo:before {
    content: "";
    position: absolute;
    width: 290px;
    height: 420px;
    border: 5px solid blueviolet;
    left: 15px;
    top: -15px;
    z-index: -1;

}

@media screen and (max-width: 980px ) {
    .container {
        flex-direction: column;
    }
}

@media screen and (max-width: 480px ) {
    .greeting span:nth-child(2) span {
        font-weight: 300;
        font-stretch: normal;
        font-size: 40px;
        line-height: 1.2;
        letter-spacing: 3px;
        font-family: 'Montserrat', sans-serif;
        position: relative;
        z-index: 0;

    }
    .greeting span:nth-child(1) {
      display: flex;
        flex-direction: column;
        align-items: flex-start;

    }

    .greeting span:nth-child(2) {
        font-weight: 300;
        font-stretch: normal;
        font-size: 40px;
        line-height: 1.2;
        letter-spacing: 3px;
        font-family: 'Montserrat', sans-serif;
        padding-right: 25px;

    }

}
