.skills {
    height: 100vh;
    background-color: #252527;
    display: flex;
    justify-content: center;
}

.container {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 100px 0;
}

.headerTitle {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    line-height: 1.2;
    letter-spacing: 5px;
    font-weight: 400;
    font-size: 36px;
    text-transform: capitalize;
}

.line {
    height: 1px;
    width: 55px;
    margin: 30px 0;
    background-color:blueviolet;
}

.skillsWrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
}

.skill {
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon {
    height: 80px;
    width: 80px;
    background-color: #a8a8a8;
}
.discription {
    text-align: center;
}

@media screen and (max-width: 1140px ) {
    .skillsWrapper {
        flex-direction: column;
        align-items: center;
    }
}

@media screen and (max-width: 1128px ) {
    .skill {
        width: 500px;
    }
}

@media screen and (max-width: 630px ) {
    .skill {
        width: 100%;
    }
}
