.slogan {
    height: 250px;
    background-color: #ff0c0e;
    display: flex;
    justify-content: center;
}
.container {
    width: 80%;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.sloganItem {
    display: flex;
    height: 80%;
    flex-direction: column;
    justify-content: space-around;
}
